<template>

  <div class="mb-5">
    <div>
      <h2 class="title font-weight-semibold text-center mb-4">CONTATTACI</h2>
      <p class="text-4 opacity-8 text-center mb-4">Per qualsiasi informazione o preventivo<br> siamo a tua disposizione.</p>
    </div>

    <div class="form-group"
         :class="[
       {'input-group': hasIcon},
       {'has-danger': error},
       {'input-group-focus': focused},
       {'has-label': label || $slots.label}]">
      <slot name="label">
        <label v-if="label" :class="labelClasses">
          {{label}}
          <span v-if="required">*</span>
        </label>
      </slot>

      <slot name="addonLeft">
        <div v-if="addonLeftIcon" class="input-group-prepend">
          <i class="input-group-text" :class="addonLeftIcon"></i>
        </div>
      </slot>

      <slot>
        <div class="container">
          <div class="row">
            <div class="col-lg-6 col-md-6 col-sm-12 col-xs-12">
              <input
                  placeholder="Nome"
                  :value="value"
                  v-on="listeners"
                  v-bind="$attrs"
                  v-model="firstName"
                  class="form-control"
                  :class="[{valid: value && !error}, inputClasses]"
                  aria-describedby="addon-right addon-left">
            </div>

            <div class="col-lg-6 col-md-6 col-sm-12 col-xs-12">
              <input
                  placeholder="Telefono"
                  :value="value"
                  v-on="listeners"
                  v-bind="$attrs"
                  class="form-control"
                  :class="[{valid: value && !error}, inputClasses]"
                  aria-describedby="addon-right addon-left">
            </div>

            <div class="col-lg-12 col-md-12 col-sm-12 col-xs-12">
              <input
                  placeholder="Indirizzo e-mail"
                  :value="value"
                  v-on="listeners"
                  v-bind="$attrs"
                  class="form-control"
                  :class="[{valid: value && !error}, inputClasses]"
                  aria-describedby="addon-right addon-left">
            </div>

            <div class="col-lg-12 col-md-12 col-sm-12 col-xs-12">
            <textarea
                placeholder="Messaggio"
                :value="value"
                v-on="listeners"
                v-bind="$attrs"
                class="form-control"
                :class="[{valid: value && !error}, inputClasses]"
                cols="65" rows="10">
          </textarea>
            </div>

          </div>
        </div>
      </slot>

      <slot name="addonRight">
        <span v-if="addonRightIcon" class="input-group-addon input-group-append">
          <i class="input-group-text" :class="addonRightIcon"></i>
        </span>
      </slot>

      <slot name="infoBlock"></slot>
      <slot name="helpBlock">
        <div class="text-danger invalid-feedback" style="display: block;" :class="{'mt-2': hasIcon}" v-if="error">
          {{ error }}
        </div>
      </slot>

    </div>
    <buttons-section class="btn mt-5">Invia</buttons-section>
  </div>

</template>
<script>
import ButtonsSection from "./../../pages/components/BasicElements/ButtonsSection.vue"

  export default {
    components: {
      ButtonsSection
    },
    inheritAttrs: false,
    firstName: null,
    name: 'fg-input',

    props: {
      required: Boolean,
      label: String,
      error: String,
      labelClasses: String,
      inputClasses: String,
      value: {
        type: [String, Number],
        default: ''
      },
      addonRightIcon: String,
      addonLeftIcon: String
    },
    data() {
      return {
        focused: false
      }
    },
    computed: {
      listeners() {
        return {
          ...this.$listeners,
          input: this.updateValue,
          focus: this.onFocus,
          blur: this.onBlur
        }
      },
      hasIcon() {
        const {addonRight, addonLeft} = this.$slots;
        return addonRight !== undefined || addonLeft !== undefined || this.addonRightIcon !== undefined || this.addonLeftIcon !== undefined
      }
    },
    methods: {
      updateValue(evt) {
        let value = evt.target.value;
        this.$emit('input', value);
      },
      onFocus(value){
        this.focused = true;
        this.$emit('focus', value);
      },
      onBlur(value){
        this.focused = false;
        this.$emit('blur', value);
      }
    }
  }
</script>
<style scoped>

input,
textarea {
  margin: 10px;
}

.btn {
  width: 15rem;
  height: 3.5rem;
  text-transform: uppercase;
}

.btn:hover {
  animation: boing 300ms ease-in-out;
}

@keyframes boing {
  15%, 40%, 75%, 100% {
    transform-origin: center center;
  }
  15% {
    transform: scale(1.2, 1.1);
  }
  40% {
    transform: scale(0.95, 0.95);
  }
  75% {
    transform: scale(1.05, 1);
  }
  100% {
    transform: scale(1, 1);
  }
}

</style>
